.btn_1 {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  font-weight: 600;
  color: #fff;
  background-color: #706fe5;
  transition: 0.3s ease;
  text-decoration: none;
}
.btn_1:hover {
  background-color: rgba(112, 111, 229, 0.8);
}

.btn_1 {
  min-width: 18rem;
  height: 4.8rem;
